<template>
  <v-app>
    <v-toolbar app>
      <v-img
        v-if="isHomeCare"
        :src="require('@/assets/chc-logo.png')"
        height="90"
        width="300"
        :contain="true"
      ></v-img>
      <v-img
        v-if="isHospice"
        :src="require('@/assets/gsh-logo.png')"
        height="90"
        width="300"
        :contain="true"
      ></v-img>

      <v-toolbar-title class="headline text-uppercase">
        <span d-inline class="font-weight-light">Physician Referral Form</span>
      </v-toolbar-title>
    </v-toolbar>

    <v-content>
      <HomeCareReferralForm v-if="isHomeCare"/>
      <HospiceReferralForm v-if="isHospice"/>
    </v-content>
  </v-app>
</template>

<script>
import HomeCareReferralForm from "./components/HomeCareReferralForm";
import HospiceReferralForm from "./components/HospiceReferralForm";

const NEW_BASE = "https://api002.chsli.org/apps/physician-referral-form/";

export default {
  name: "App",
  components: { HomeCareReferralForm, HospiceReferralForm },

  beforeCreate() {
    const params = new URLSearchParams(location.search);
    if (params.has("noredirect")) return; // handy for local dev

    const form = params.get("form") || "homecare";
    const target = `${NEW_BASE}?form=${encodeURIComponent(form)}`;

    const here = new URL(location.href);
    const newURL = new URL(target);
    const alreadyOnNew =
      here.origin === newURL.origin &&
      here.pathname.startsWith(newURL.pathname);

    if (!alreadyOnNew) {
      // Fallback note if JS fails
      const addFallback = () => {
        const p = document.createElement("p");
        p.style.textAlign = "center";
        p.style.marginTop = "2rem";
        p.innerHTML = `If you're not redirected automatically, <a href="${target}">click here</a>.`;
        document.body.appendChild(p);
      };
      document.readyState === "loading"
        ? document.addEventListener("DOMContentLoaded", addFallback)
        : addFallback();

      location.replace(target);
    }
  },

  data() {
    return { form: null };
  },

  beforeMount() {
    const params = new URLSearchParams(location.search);
    this.form = params.get("form");
  },

  computed: {
    isHomeCare() { return this.form === "homecare"; },
    isHospice()  { return this.form === "hospice"; }
  }
};
</script>


<style>
.v-toolbar,
.v-toolbar__content {
  height: 100px !important;
}

.v-toolbar__content .v-image__image {
  width: 300px !important;
}

.v-content {
  padding-top: 100px !important;
}
</style>
